export default {
	data() {
		return {
			menu: ['最热新品', '最近上新', '即将开售', '新品手机'],		//导航栏文字数据
			curr: 0,				//导航栏初始下标
			index: ''
		}
	},
	methods:{
		setCurr(e) {			//轮播页面点击事件
			let thisCurr = e.detail.current || e.currentTarget.dataset.id || 0;
			this.curr = thisCurr;
		}
	}
}
